import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ResourcesService } from '../../../core/core-data/resources/resources.service';
import { ResourceData } from '../../../core/core-data/resources/resourcedata';

@Component({
  selector: 'app-resource-view',
  templateUrl: './resource-view.component.html',
  styleUrls: ['./resource-view.component.scss']
})

export class ResourceViewComponent implements OnInit {
  resourceData: ResourceData;
  constructor(private resourceService: ResourcesService, private activatedRoute: ActivatedRoute) {}
  ngOnInit(): void {
    this.getResourceDetails();
  }

  /**
   * @method getResourceDetails
   * @description Get details of single resource
   */
  getResourceDetails = () => {
    this.resourceService.getResource(this.activatedRoute.snapshot.params.resource).subscribe(res => {
      this.resourceData = res;
    });
  }

  /**
   * @method open
   * @description Open link in new window
   */
  open = () => {
    window.open(this.resourceData.resume, '_blank');
  }
}

